/* Center the My Blog heading */


.bruh {
    text-align: center; /* Center the heading */
    margin-top: 20px; /* Adjust the top margin */
    margin-bottom: 10px; /* Reduce the spacing below the heading */
    font-size: 2.5rem; /* Optional: Increase the font size */
  }
  
  /* Reduce spacing between the heading and the content */
  .story {
    margin-top: 0px; /* Eliminate extra spacing */
    padding: 20px; /* Optional: Add padding for a more structured look */
  }
  
  /* Optional: Center the content itself */
  .story-content {
    max-width: 800px; /* Set a max-width for better readability */
    margin: 0 auto; /* Center the content horizontally */
    text-align: justify; /* Justify text for a clean look */
  }
  
  /* Loader styles if needed */
  .loader {
    display: block;
    margin: 0 auto;
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  