/* components/Navbar.css */

.navbar {
    display: flex;
    justify-content: flex-end; /* Flex items to the right */
    padding: 10px 20px;
  }
  
  .navbar-list {
    list-style-type: none;
    display: flex;
    gap: 20px; /* Add space between the links */
  }
  
  .navbar-list li {
    margin: 0;
  }
  
  .navbar-list a {
    text-decoration: none;
    color: #000;
    font-size: 16px;
  }
  
  .navbar-list a:hover {
    color: #007bff; /* Change color on hover */
  }
  